import { Component, OnInit } from '@angular/core';
import { SupplerService } from '../_services/suppler.service';

@Component({
  selector: 'app-suppler-invoice',
  templateUrl: './suppler-invoice.component.html',
  styleUrls: ['./suppler-invoice.component.scss']
})
export class SupplerInvoiceComponent implements OnInit {

  articalCategoryID:any;
  articalID:any;
  supplerID:any;
  orderNumber:any;
  invoiceNumber:any;
  totalPrice:any;
  quantity:any;
  comment:any;
  buying:any;
  selling:any;
  obj:any;
  _id:any;

  constructor( public SupplerService:SupplerService) { }

  ngOnInit(): void {
    this.buying = false;
    this.selling = false;
    this.view();
  }

  //view
  view(){
    this.SupplerService.viewSupplerInvoice().subscribe(
      (res)=>{
        this.obj = res['data'];
        console.log(res);
      },
      (err)=>{
        console.log(err);
      }
    )
  }

//add
  add(){
    const _add = {
      articalCategoryID:this.articalCategoryID,
      articalID:this.articalID,
      supplerID:this.supplerID,
      orderNumber:this.orderNumber,
      invoiceNumber:this.invoiceNumber,
      totalPrice:this.totalPrice,
      quantity:this.quantity,
      comment:this.comment,
      buying:this.buying,
      selling:this.selling
    }
    this.SupplerService.AddSupplerInvoice(_add).subscribe(
      (res)=>{
        this.view();
        console.log(res);
      },
      (err)=>{
        console.log(err);
      });
  }

//delete
  delete(_deleteID){
    const del = {_id:_deleteID};

    this.SupplerService.DeleteSupplerInvoice(del).subscribe(
      (res)=>{
        this.view();
        console.log(res);
      },
      (err)=>{
        console.log(err);
      });
  }

//Active
  Active(_activeID){
    const del = {_id:_activeID};

    this.SupplerService.ActiveSupplerInvoice(del).subscribe(
      (res)=>{
        this.view();
        console.log(res);
      },
      (err)=>{
        console.log(err);
      });
  }


}
