import { Component, OnInit } from '@angular/core';
import { SupplerService } from '../_services/suppler.service';

@Component({
  selector: 'app-suppler',
  templateUrl: './suppler.component.html',
  styleUrls: ['./suppler.component.scss']
})
export class SupplerComponent implements OnInit {

  articalCategory:any;
  details:any;
  name:any;
  street:any;
  city:any;
  telephone:number;
  mobile:number;
  email:string;
  regNo:any;
  obj:any;

  constructor(public supplerService:SupplerService) { }

  ngOnInit(): void {
    this.view();
  }

  //view
  view(){
      this.supplerService.viewSuppler().subscribe(
        (res)=>{
          this.obj = res['data'];
          console.log(res);
        },
        (err)=>{
          console.log(err);
        }
      )
  }

  //add 
  add(){
    const suppler = {

      articalCategory:this.articalCategory,
      details:this.details,
      name:this.name,
      street:this.street,
      city:this.city,
      telephone:this.telephone,
      mobile:this.mobile,
      email:this.email,
      regNo:this.regNo,
    }
    console.log(suppler);

    this.supplerService.AddSuppler(suppler).subscribe(
      (res)=>{
        console.log(res);
        this.view();
      },
      (err)=>{
        console.log(err);
      });
  }

  //delete
  delete(_deleteID){

    const del = {_id:_deleteID};

     this.supplerService.DeleteSuppler(del).subscribe(
       (res)=>{
         this.view();
         console.log(res);
       },
       (err)=>{
         console.log(err);
       });
  }

  //Active
  Active(_deleteID){
    const del = {_id:_deleteID};

     this.supplerService.ActiveSuppler(del).subscribe(
       (res)=>{
         this.view();
         console.log(res);
       },
       (err)=>{
         console.log(err);
       });
  }

  //update
  update(_id){

  }

}
